			// 云打包在manifest中配置相关模块和SDK信息，离线打包在原生工程中配置。某个服务商配置被打包进去，运行时就能得到相应的服务供应商。
			let getP = await this.getProv()
			
			
			
			// 获取提供商成功，发送数据给后台
			if(getP) {
				console.log('获取服务提供商成功')
				
